.container {
  height: 200px;
  width: 800px;
  padding: 20px;
  border: 1px solid black;
  border-radius: 10px;
  background-color: rgb(226, 226, 227);
  margin: 20px auto;
}

.post_id {
  font-size: 22px;
  margin-bottom: 20px;
}

.title {
  font-size: 18px;
}

.button {
  margin-top: 20px;
  color: pink;
}

.button:hover {
  cursor: pointer;
}
